
@import url('https://fonts.googleapis.com/css?family=Dosis:400|Cookie');
body, html, .outer, .inner {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
* {
  font-family: 'Cookie', cursive;
  box-sizing: border-box;
  color: #332c33;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.outer {
  display: table;
}
.inner {
  display: table-cell;
  /*vertical-align: middle;*/
}
.container {
  text-align: center;
}
header {
  padding: 15px;
  width: 320px;
  margin: auto;
  position: relative;
}
main {
  clear: both;
  padding: 15px;
}
main * {
  font-family: 'Dosis', sans-serif;
  font-size: 15px;
}
img.logo {
  width: 150px;
  border-radius: 50%;
}
.container h1 {
  margin: 0;
  font-size: 37px;
  position: relative;
  left: -1px;
  margin-top: 1px;
  margin-bottom: -11px;
}
.social-icons {
  position: absolute;
  top: 36px;
  left: -14px;
}
.social-icons img {
  width: 30px;
  height: 30px;
}
.social-icons a {
  position: relative;
  text-decoration: none;
}
.social-icons .fb {
  left: 59px;
}
.social-icons .in {
  top: 39px;
  left: 20px;
}
.social-icons .tw {
  top: 78px;
  left: -7px;
}
.social-icons .hover-text {
  position: absolute;
  top: -13px;
  left: -124px;
  width: 120px;
  text-align: right;
  font-size: 20px;
}
.social-icons .fb .hover-text {
  left: -125px;
}
.menu-links {
  position: absolute;
  top: 36px;
  right: -13px;
}
.menu-links .hover-text {
  position: absolute;
  top: 1px;
  left: 37px;
  width: 60px;
  text-align: left;
  font-size: 20px;
}
.menu-links section {
  float: left;
}
.menu-links a {
  background: #332c33;
  color: white;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: inline-block;
  cursor: pointer;
  font-size: 23px;
  line-height: 1.4;
  position: relative;
  text-decoration: none;
}
.menu-links a span {
  color: white;
}
.menu-links a.R {
  top: 39px;
  left: -22px;
  font-size: 22px;
}
.menu-links a.T {
  top: 78px;
  left: -60px;
  padding-right: 2px;
  padding-top: 1px;
}
.menu-links a.T span {
}
.menu-links .active a span {
  font-weight: bold;
}
article p {
  -webkit-margin-before: 0.6em !important;
  -webkit-margin-after: 0.2em !important;
}

/* transitions */
.fade-enter-active, .fade-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
}
.fade-enter-active {
  transition-delay: .25s;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.fadePhoto-enter-active, .fadePhoto-leave-active {
  transition-property: opacity;
  transition-duration: .2s;
}
.fadePhoto-enter, .fadePhoto-leave-active {
  opacity: 0
}
.invisible {
  opacity: 0;
}



